<template>
  <div class="box">
    <div class="faceId">
      <div class="face_Id">
        <video 
          @loadedmetadata="onPlay" 
          ref='video'
          id="inputVideo" 
          autoplay="" 
          muted=""
          playsinline="">
        </video>
        <canvas id="overlay"></canvas>
        <canvas id='snap' width="640" height="480"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      forwardTimes: [],
      withBoxes: true,
      scores: [],
      stream: undefined,
    }
  },

  mounted() {
    this.run()
  },

  methods: {
    avg(array) {
      //封装求平均值函数
      var len = array.length;
      var sum = 0;
      for(var i = 0;i<len;i++){
          sum +=array[i];
      }
      return sum/len;
    },

    async onPlay() {
      const videoEl = $('#inputVideo').get(0)
      if (videoEl.paused || videoEl.ended || !isFaceDetectionModelLoaded())
        return setTimeout(() => this.onPlay())
      const options = getFaceDetectorOptions()
      const ts = Date.now()
      const result = await faceapi.detectSingleFace(videoEl, options)
      // .withFaceLandmarks()
      // console.log(result)
      if (!!result) {
        const canvas = $('#overlay').get(0)
        const dims = faceapi.matchDimensions(canvas, videoEl, true)
        const resizedResult = faceapi.resizeResults(result, dims)
        if (this.withBoxes) {
          faceapi.draw.drawDetections(canvas, resizedResult)
        }
        // faceapi.draw.drawFaceLandmarks(canvas, resizedResult)
      }

      if (!!result) {
        // console.log(result.score)
        this.scores.push(result.score)
        if (this.scores.length>20) {
          this.scores.shift()
        }
      }

      console.log(this.avg(this.scores)+"====="+this.scores.length);
      

      if(result && this.scores.length == 20 && this.avg(this.scores) > 0.6 && result.score>0.9) {
        let snapConvas = document.getElementById('snap')      
        snapConvas.getContext("2d").drawImage(videoEl, 0, 0, snapConvas.width, snapConvas.height)
        $(snapConvas).show()
        this.turnOffCamera()

        let dataURL = snapConvas.toDataURL("image/jpeg", 1.0);
        this.$emit('capture', dataURL)
      } else {
        setTimeout(() => this.onPlay(), 10)
      }
    },

    turnOffCamera() {
      let video = $('#inputVideo').get(0)
      video.pause()
      video.srcObject = undefined;
      console.log(this.stream.getTracks())
      this.stream.getTracks()[0].stop();
    },

    testVideo() {
       let video = this.$refs.video
       let constraints = {
         video: { width: 640, height: 480},
         audio: false
       }

       if (navigator.mediaDevices === undefined) {
           navigator.mediaDevices = {};
       }
       if (navigator.mediaDevices != undefined && navigator.mediaDevices.getUserMedia != undefined) { // 标准的API
           let _this = this
           console.log('hehehehee');
           
           navigator.mediaDevices.getUserMedia(constraints).then(stream => {
              video.srcObject = stream;
              _this.stream = stream
           });
       } else if (navigator.webkitGetUserMedia != undefined) { // WebKit 核心的API
           navigator.webkitGetUserMedia(constraints, stream => {
               video.src = window.webkitURL.createObjectURL(stream);
           }, err => {
               alert("kitErr: " + err.message);
           });
       } else {
           alert("defaultErr: " + err.message);
       }
     },

    async run() {
      await changeFaceDetector()
      await faceapi.loadFaceLandmarkModel('/model/')
      this.testVideo()
    }
  }
}
</script>

<style scoped>
  .box {
    width: 280px;
    height: 280px;
    position: relative;
    overflow: hidden;
  }

  .face_Id {
    position: absolute;
    left: -180px;
    height: -100px;
    width: 640px;
    height: 480px;
  }

  .faceid {
    position: relative;
  }

  .video {
    position: relative;
    width: 640px;
    height: 480px;
    transform-origin: left top;
  }

  video {
    height: 100%;
    width: 100%;
    object-fit: fill;
  }

  #snap {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
  }

  #overlay {
    position: absolute;
    top: 0;
    left: 0;
    transform-origin: left top;
  }
</style>
